import React from 'react';
import * as RouterDom from 'react-router-dom';
import { Grid, 
        IconButton, 
        Link, 
        Tooltip, 
        Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import TreeRecursiveView from '../components/TreeView';
import { data } from '../components/Data.js';
import '@fontsource/roboto';
import '../assets/css/Pages.css';

import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextOutlinedIcon from '@material-ui/icons/NavigateNextOutlined';

const useStyles = makeStyles(() => ({
    root: {
        flexGrow: 1,
    },
    container: {
        padding: '65px 20px 0px 110px',
    },
    header: {
        paddingTop: '125px',
        paddingLeft: '65px'
    },
    nextPage: {
        paddingLeft: '50px',
        paddingTop: '20px',
    }
}));

function Development(props) {
    const classes = useStyles();

    return (
        <div className={ classes.root }>
            <Grid container spacing={ 2 }>

                <Grid item xs={ 12 }>
                    <Navbar location='Desarrollo' />
                </Grid>
                
                <Grid container spacing={ 2 } className={ classes.container }>
                    <Grid item xs={ 12 } className={ classes.header }>
                        <Typography variant='h2' component='h2'>
                            Desarrollo de la prctica
                        </Typography>
                    </Grid>
                </Grid>

                <Grid container spacing={ 3 } className={ classes.container }>

                    <Grid item xs={ 12 } sm={ 12 } md={ 6 } lg={ 6 } xl={ 6 }> 
                        <Typography variant='body1' component='p'>
                            Para el desarrollo de la prctica, se utiliz el editor 
                            de texto&nbsp;<Link href='https://code.visualstudio.com/'>
                                Visual Studio Code
                            </Link>, junto con extensiones para
                            HTML, CSS, JavaScript, React.js y GitHub.
                            <br /><br />
                            Asimismo, una vez creado el proyecto, se abre la carpeta 
                            del mismo a travs de Visual Studio Code y se comienza con
                            la reestructuracin del proyecto y, la creacin de los archivos
                            a utilizar.
                            <br /><br />
                            Cabe destacar que, todos los elementos que culminen con un punto ( . ) seguido
                            de alguna extensin, corresponde a archivos, mientras que, aquellos que no tengan
                            esto, representan las carpetas. Tambin es importante mencionar que no se resaltan
                            los archivos en carpetas de imgenes y/o que proporcionan informacin para algunos
                            componentes.
                            <br /><br />
                            Tras eliminar, crear y reestructurar tanto las carpetas como los
                            archivos del proyecto, se comienza con el desarrollo de la aplicacin.
                            <br /><br />
                            Como se est implementando el framework React.js para esta prctica,
                            se decide trabajar con la filosofa de esta biblioteca, es decir, diseo
                            por componentes. De esta manera, se fragmenta porciones de interfaces con
                            el fin de facilitar la legibilidad y mantenimiento de una pgina.
                            <br /><br />
                            Lo primero a realizar es la estructura de las pginas, por medio del 
                            componente "Grid" de Material UI, el cual implementar estructura de 
                            cuadros o Flex Box en una pgina y, a su vez, facilitar el diseo 
                            responsivo.
                            <br /><br />
                            De igual manera, React.js trabaja con la sintaxis JSX, lo que permite
                            la construccin de componentes de una forma similar a HTML, haciendo que
                            el cdigo sea legible y fcil desarrollar.
                            <br /><br />
                            Luego de ello, se comenz a desarrollar los componentes principales;
                            CardProfile.js, Cards.js, DataList.js, DialogImage.js, Footer.js, Graphics.js, 
                            HomeButtons.js, Navbar.js, Table.js y TreeView.js.
                            <br /><br />
                            Una vez desarrollados los componentes, a armar cada pgina, situando cada 
                            componente en el correspondiente "Grid", dndole forma a la pgina.
                            <br /><br />
                            Durante el proceso de desarrollo de cada pgina, se puede ir colocando las
                            rutas de redireccionamiento en el archivo Routes.js, con el fin de comprobar que
                            los redireccionamientos entre pgina funcionan sin inconvenientes.
                            <br /><br />
                            Tambin, con la construccin de cada pgina se puede ir creando las hojas de estilo (CSS),
                            para darle mejor aspecto a las pginas o componentes que requieran una profunda modificacin.
                            <br /><br />
                            Por ltimo, se agregaron las pginas para el "Error 404", una carga visual de pgina en el 
                            tope superior y la configuracin de colores para el tema de la pgina. La pgina de Error 
                            404 se le adjuntaron componentes de React VFX, para un aspecto ms acorde con la temtica 
                            escogida para la prctica. 
                            <br /><br />
                            De igual forma, para la carga de pgina ubicado en el tope superior, se realiz un componente
                            exclusivo para el archivo Route.js, el cual es el archivo CustomSwitch.js. Este componente
                            hace uso de la TopBarProgress de la librera "react-topbar-progress-indicator". Una vez desarrollado,
                            se adjunta al archivo Route.js como un componente que contendr todas las rutas accesibles por la pgina.
                            <br /><br />
                            Por ltimo, se defini el tema y la paleta de colores que utilizara la prctica. Esto fue ubicado en
                            una carpeta llamada "theme" en el apartado de componentes. All se desarroll un archivo Colors.js que
                            contiene la paleta de colores a utilizar, y otro archivo Theme.js, el cual define cada color para cada 
                            elemento que tendr la paleta de colores.
                        </Typography>
                    </Grid>

                    <Grid item xs={ 12 } sm={ 12 } md={ 6 } lg={ 6 } xl={ 6 } justify='center'> 
                        <Typography>
                            La estructura de las carpetas y archivos quedaran de la siguiente forma:
                            <br /><br />
                        </Typography>
                        <TreeRecursiveView data={ data } />
                    </Grid>

                </Grid>

                <Grid item xs={ 12 } justify='center'>
                    <Typography align='center' className={ classes.nextPage }>
                        <Tooltip title='Instalacin' aria-label='Instalacin'>
                            <IconButton component={ RouterDom.Link } to='/Installation' color='primary'>
                                <NavigateBeforeIcon />
                            </IconButton>
                        </Tooltip>
                        <Tooltip title='Libreras' aria-label='Libreras'>
                            <IconButton component={ RouterDom.Link } to='/Libraries' color='primary'>
                                <NavigateNextOutlinedIcon />
                            </IconButton>
                        </Tooltip>
                    </Typography>
                </Grid>
    
                <Grid item xs={ 12 } >
                    <Footer />
                </Grid>
                
            </Grid>
        </div>
    );
}

export default Development;