import React from 'react';
import * as RouterDom from 'react-router-dom';
import { Button, 
        Divider, 
        Grid, 
        IconButton, 
        Tooltip, 
        Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import DialogImage from '../components/modal/DialogImage';
import '@fontsource/roboto';
import '../assets/css/Pages.css';

import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextOutlinedIcon from '@material-ui/icons/NavigateNextOutlined';

import ReactRouterPage from '../assets/img/react-router-dom.png';
import MaterialUIPage from '../assets/img/material-ui.png';
import ReactChartjs2Page from '../assets/img/react-chartjs-2.png';
import ReactVFXPage from '../assets/img/react-vfx.png';
import Error404Page from '../assets/img/error404.png';
import RepoTopBar from '../assets/img/repoTopbar.png';
import TopBarProgress from '../assets/img/TopBarProgress.png';
import GHPages from '../assets/img/gh-pages.png';

const useStyles = makeStyles(() => ({
    root: {
        flexGrow: 1,
    },
    container: {
        padding: '65px 20px 0px 110px',
    },
    header: {
        paddingTop: '125px',
        paddingLeft: '65px'
    },
    nextPage: {
        paddingLeft: '50px',
        paddingTop: '20px',
    }
}));

function Libraries(props) {
    const classes = useStyles();

    return (
        <div className={ classes.root }>
            <Grid container spacing={ 3 }>

                <Grid item xs={ 12 }>
                    <Navbar location='Libreras' />
                </Grid>
                
                <Grid container spacing={ 2 } className={ classes.container }>
                    <Grid item xs={ 12 } className={ classes.header }>
                        <Typography variant='h2' component='h2'>
                            Libreras utilizadas
                        </Typography>
                    </Grid>

                    <Grid item xs={ 12 }>
                        <Typography variant='body1' component='p'>
                            Adems de hacer uso del framework React.js, tambin se implementaron
                            algunos componentes, elementos y funciones de otras libreras para el
                            desarrollo de la prctica.
                        </Typography>
                    </Grid>

                    <Grid item xs={ 12 }>
                        <Divider />
                    </Grid>

                    <Grid item xs={ 12 } sm={ 12 } md={ 6 } lg={ 6 } xl={ 6 }>
                        <Typography variant='h4' component='h4'>
                            <b>React Router Dom </b>
                        </Typography>
                        <Typography variant='body1' component='p'>
                            Esta librera se utiliz con el fin de facilitar el redireccionamiento
                            entre las pginas desarrolladas para esta prctica.
                            <br /><br />
                            Su instalacin es por medio del comando 'npm i react-router dom', el cual
                            debe ejecutarse en el interior de la carpeta del proyecto.
                            <br /><br />
                            Para su uso, basta con importar el componente a utilizar al inicio del archivo
                            con extensin js.
                            <br /><br />
                            El material de apoyo utilizado es la documentacin que se encuentra en su 
                            pgina oficial:
                            <br /><br />
                            <Typography align='center'>
                                <Button variant='contained' color='primary' href='https://reactrouter.com/'>
                                    React Router Dom
                                </Button>
                            </Typography>
                        </Typography>
                    </Grid>

                    <Grid item xs={ 12 } sm={ 12 } md={ 6 } lg={ 6 } xl={ 6 } align='center'>
                        <DialogImage 
                            button={ <img src={ ReactRouterPage } alt='Pgina de React Router Dom' width='400px' /> } 
                            modalTitle='Pgina oficial de React Router Dom'
                            content={ <img src={ ReactRouterPage } alt='Pgina de React Router Dom' width='565px' /> }
                        />
                    </Grid>

                    <Grid item xs={ 12 }>
                        <Divider />
                    </Grid>

                    <Grid item xs={ 12 } sm={ 12 } md={ 6 } lg={ 6 } xl={ 6 }>
                        <Typography variant='h4' component='h4'>
                            <b>Material UI</b>
                        </Typography>
                        <Typography variant='body1' component='p'>
                            Material UI es una librera para React.js de componentes grficos
                            altamente personalizables, sin necesidad de una configuracin rigurosa
                            en el proyecto a desarrollar.
                            <br /><br />
                            Su instalacin es por medio del comando 'npm i @material-ui/core', el cual
                            debe ejecutarse en el interior de la carpeta del proyecto.
                            <br /><br />
                            Asimismo, la pgina oficial de Material UI recomienda utilizar como fuente
                            "Roboto", la cual se puede instalar por medio del comando 'npm i @fontsource/roboto'.
                            Posterior a ello, para que la fuente aparezca en el proyecto, se llama en el archivo
                            js que redizar la pgina por medio de la lnea de cdigo "import '@fontsource/roboto';".
                            <br /><br />
                            Tambin se implementaron los iconos que provee Material UI, para ello se requiere 
                            instalar la librera de iconos con el comando 'npi i @material-ui/icons'. Para su 
                            uso basta con importar el icono deseado al comienzo del archivo js en donde se vaya 
                            a implementar.
                            <br /><br />
                            Por ltimo, tambin se utilizaron componentes experimentales de la librera "El Laboratorio"
                            de Material UI. Al igual que la instalacin de las libreras anteriores, para utilizar estos
                            componentes habra que instalar la librera con el comando 'npi i @material-ui/lab'.
                            <br /><br />
                            El material de apoyo utilizado fue toda la documentacin que se puede encontrar en su 
                            pgina oficial.
                            <br /><br />
                            <Typography align='center'>
                                <Button variant='contained' color='primary' href='https://material-ui.com/'>
                                    Material UI
                                </Button>
                            </Typography>
                        </Typography>
                    </Grid>

                    <Grid item xs={ 12 } sm={ 12 } md={ 6 } lg={ 6 } xl={ 6 } align='center'>
                        <DialogImage 
                            button={ <img src={ MaterialUIPage } alt='Pgina de Material UI' width='400px' /> } 
                            modalTitle='Pgina oficial de Material UI'
                            content={ <img src={ MaterialUIPage } alt='Pgina de Material UI' width='565px' /> }
                        />
                    </Grid>

                    <Grid item xs={ 12 }>
                        <Divider />
                    </Grid>

                    <Grid item xs={ 12 } sm={ 12 } md={ 6 } lg={ 6 } xl={ 6 }>
                        <Typography variant='h4' component='h4'>
                            <b>react-chartjs-2</b>
                        </Typography>
                        <Typography variant='body1' component='p'>
                            Una librera que provee componentes para generar grficos de manera
                            sencilla.
                            <br /><br />
                            Para la implementacin de los componentes de esta librera basta con generar
                            un archivo js con las configuraciones necesarias por el desarrollador y 
                            proveerle los datos para as construir el grfico deseado.
                            <br /><br />
                            El material de apoyo utilizado es el provisto por su repositorio en GitHub.
                            <br /><br />
                            <Typography align='center'>
                                <Button variant='contained' color='primary' href='https://reactchartjs.github.io/react-chartjs-2/#/'>
                                    react-chartjs-2
                                </Button>
                            </Typography>
                        </Typography>
                    </Grid>

                    <Grid item xs={ 12 } sm={ 12 } md={ 6 } lg={ 6 } xl={ 6 } align='center'>
                        <DialogImage 
                            button={ <img src={ ReactChartjs2Page } alt='Pgina de react-chartjs-2' width='400px' /> } 
                            modalTitle='Pgina oficial de react-chartjs-2'
                            content={ <img src={ ReactChartjs2Page } alt='Pgina de react-chartjs-2' width='565px' /> }
                        />
                    </Grid>

                    <Grid item xs={ 12 }>
                        <Divider />
                    </Grid>

                    <Grid item xs={ 12 } sm={ 12 } md={ 6 } lg={ 6 } xl={ 6 }>
                        <Typography variant='h4' component='h4'>
                            <b>React VFX</b>
                        </Typography>
                        <Typography variant='body1' component='p'>
                            Una pequea librera con el propsito de aadir animaciones estilo
                            VFX a ciertos elementos grficos tales como texto, imgenes y/o vdeos.
                            <br /><br />
                            La instalacin de esta librera procede mediante el comando 'npm i react-vfx'.
                            <br /><br />
                            Los componentes de esta librera fueron utilizados nicamente para la pgina del
                            Error 404 de esta prctica, precisamente en la imagen del cono y en el texto que
                            informa el error ocurrido, aadindole efectos "Glitch" a estos elementos.
                            <br /><br />
                            El material de apoyo utilizado fueron los ejemplos mostrados en la pgina oficial
                            y el repositorio de la librera en GitHub de React VFX.
                            <br /><br />
                            <Typography align='center'>
                                <Button variant='contained' color='primary' href='https://amagi.dev/react-vfx/'>
                                    React VFX
                                </Button>
                            </Typography>
                        </Typography>
                    </Grid>

                    <Grid item xs={ 12 } sm={ 12 } md={ 6 } lg={ 6 } xl={ 6 } align='center'>
                        <DialogImage 
                            button={ <img src={ ReactVFXPage } alt='Pgina de React VFX' width='400px' /> } 
                            modalTitle='Pgina oficial de React VFX'
                            content={ <img src={ ReactVFXPage } alt='Pgina de React VFX' width='565px' /> }
                        />
                        <DialogImage 
                            button={ <img src={ Error404Page } alt='Pgina del Error 404' width='400px' /> } 
                            modalTitle='Pgina del Error 404 de la prctica'
                            content={ <img src={ Error404Page } alt='Pgina del Error 404' width='565px' /> }
                        />
                    </Grid>

                    <Grid item xs={ 12 }>
                        <Divider />
                    </Grid>

                    <Grid item xs={ 12 } sm={ 12 } md={ 6 } lg={ 6 } xl={ 6 }>
                        <Typography variant='h4' component='h4'>
                            <b>React Top Bar Progress Indicator</b>
                        </Typography>
                        <Typography variant='body1' component='p'>
                            Por ltimo, la librera React Top Bar Progress Indicator; 
                            esta librera indica el progreso de carga de la pgina que 
                            se espera visualizar.
                            <br /><br />
                            Para instalar esta librera hay que ejecutar el comando 
                            'npm i react-topbar-progress-indicator'.
                            <br /><br />
                            Este componente fue utilizado en el archivo CustomSwitch.js,
                            el componente que sutituy al Switch provisto por la librera 
                            React Router Dom.
                            <br /><br />
                            <Typography align='center'>
                                <Button variant='contained' color='primary' href='https://github.com/MoOx/react-topbar-progress-indicator'>
                                    React Top Bar Progress Indicator
                                </Button>
                            </Typography>
                        </Typography>
                    </Grid>

                    <Grid item xs={ 12 } sm={ 12 } md={ 6 } lg={ 6 } xl={ 6 } align='center'>
                        <DialogImage 
                            button={ <img src={ RepoTopBar } alt='Repositorio de la Top Bar Progress utilizada' width='400px' /> } 
                            modalTitle='Repositorio de la Top Bar Progress utilizada'
                            content={ <img src={ RepoTopBar } alt='Repositorio de la Top Bar Progress utilizada' width='565px' /> }
                        />
                        <DialogImage 
                            button={ <img src={ TopBarProgress } alt='Barra de Progresso de la Prctica' width='400px' /> } 
                            modalTitle='Barra de Progresso de la Prctica'
                            content={ <img src={ TopBarProgress } alt='Barra de Progresso de la Prctica' width='565px' /> }
                        />
                    </Grid>

                    <Grid item xs={ 12 }>
                        <Divider />
                    </Grid>

                    <Grid item xs={ 12 } sm={ 12 } md={ 6 } lg={ 6 } xl={ 6 }>
                        <Typography variant='h4' component='h4'>
                            <b>GitHub Pages</b>
                        </Typography>
                        <Typography variant='body1' component='p'>
                            Esta librera se utiliz con el propsito de realizar deploy
                            con GitHub Pages.
                            <br /><br />
                            Para su instalacin, se debe ejecutar el comando 'npm i gh-pages'.
                            Posterior a ello, se debe ajustar el archivo package.json, en donde,
                            justo debajo de la lnea de "name", se coloca "homepage" seguido del
                            url que se supone que tendr la pgina. Este url est conformado por 
                            "https://" seguido del usuario que cre el repositorio seguido de ".github.io/"
                            y por ltimo el nombre del repositorio.
                            <br /><br />
                            Para este caso, el url es "https://gustavoerivero.github.io/Lab2-Framework-React".
                            <br /><br />
                            De igual manera, en el apartado de scripts del mismo archivo package.json, se colocan:
                            <br/><br />
                            "predeploy": "npm run build",<br/>
                            "deploy": "gh-pages -d build"
                            <br /><br />
                            Para finalizar, se actualizan los cambios del repositorio haciendo "git push" y se ejecutan
                            los comandos "npm run build" seguido de "gh-pages -d build"
                            <br /><br />
                            El material de apoyo aquel que se encuentra en su repositorio:
                            <br /><br />
                            <Typography align='center'>
                                <Button variant='contained' color='primary' href='https://www.npmjs.com/package/gh-pages'>
                                    GitHub Pages
                                </Button>
                            </Typography>
                        </Typography>
                    </Grid>

                    <Grid item xs={ 12 } sm={ 12 } md={ 6 } lg={ 6 } xl={ 6 } align='center'>
                        <DialogImage 
                            button={ <img src={ GHPages } alt='Pgina de npm con la informacin de gh-pages' width='400px' /> } 
                            modalTitle='Pgina de npm con la informacin de gh-pages'
                            content={ <img src={ GHPages } alt='Pgina de npm con la informacin de gh-pages' width='565px' /> }
                        />
                    </Grid>

                </Grid>
    
                <Grid item xs={ 12 } justify='center'>
                    <Typography align='center' className={ classes.nextPage }>
                        <Tooltip title='Desarrollo' aria-label='Desarrollo'>
                            <IconButton component={ RouterDom.Link } to='/Development' color='primary'>
                                <NavigateBeforeIcon />
                            </IconButton>
                        </Tooltip>
                        <Tooltip title='Componentes' aria-label='Componentes'>
                            <IconButton component={ RouterDom.Link } to='/Components' color='primary'>
                                <NavigateNextOutlinedIcon />
                            </IconButton>
                        </Tooltip>
                    </Typography>
                </Grid>

                <Grid item xs={ 12 } >
                    <Footer />
                </Grid>
            
            </Grid>
        </div>
    );
}

export default Libraries;